@import url("https://fonts.googleapis.com/css2?family=Sunflower:wght@300&display=swap");

:root {
  --main-bg-color-deepdark: #00101b;
  --main-bg-color-dark: #01223a;
  --main-bg-color: rgb(1, 41, 69);

  --main-text-color: rgb(96, 255, 239);
  --main-text-color-dark: rgba(96, 255, 239, 0.5);
  /* --sub-text-color: rgb(222, 255, 252); */
  --sub-text-color: rgb(255 208 106);

  --chart-bg-color: rgb(1, 32, 49);
  --chart-bg-color-dark: rgb(1, 27, 45);

  --compo-bg-color: rgb(22 85 97);
  /* rgba(96, 255, 239, 0.2); */
  /* --compo-bg-color: rgba(20, 47, 65); */
  /* #ff7e5f, #feb47b */
}

body {
  background-color: var(--main-bg-color-deepdark);

  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  color: var(--sub-text-color);

  font-family: "Sunflower", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-weight: bold;
}

.gradient-background {
  background: radial-gradient(
    circle,
    var(--main-bg-color),
    var(--main-bg-color-deepdark)
  );
}

.App {
  width: 100vw;
  height: 100vh;

  text-align: center;

  display: flex;
  justify-content: center;
}

.section {
  padding: 75px 0px 0px 15px;
  display: flex;
}
.section0 {
  width: 100%;
}
.section1 {
  display: flex;
  flex-direction: column;
}

.section2 {
  width: 400px;
  display: flex;
  align-items: start;
}

.section3 {
  display: flex;
  align-content: flex-start;
  align-items: flex-start;
  justify-content: flex-start;
}

#chartContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.chartBox {
  /* width: 400px; */
  /* height: 400px; */
  margin: 10px;
}

#userContainer {
  min-width: 80px;
  margin-top: 30px;
  border-radius: 15px;
  background-color: var(--compo-bg-color);
}
.userBlock {
  font-size: 0.8rem;
  color: var(--main-text-color);
  margin: 20px 0;
}

.profile-container {
  display: flex;
  margin-top: 10px;
}
.profile-image {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
  display: block;
  margin: 0 auto;
  margin-top: 15px;
}

.leftSide {
  color: var(--main-text-color);

  border-radius: 15px;
  border-color: var(--main-text-color);
  border-width: 1px;
  border-style: solid;

  margin: 10px;
  padding: 20px;
  width: 300px;
}
.leftSideCompo1 {
  height: 200px;
}
.leftSideCompo2 {
  height: 150px;
}
.leftSideCompo3 {
  padding: 0px;
  width: 340px;
  /* height: 180px; */

  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  border: none;
}
.leftSideButton {
  margin: 5px 0;
  color: var(--main-bg-color);
  font-size: 4ex;
  border-radius: 12px;
  width: 105px;
  height: 85px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--main-text-color);
}
.leftSideButton:hover {
  background-color: var(--sub-text-color);
}
.buttonCaption {
  display: block;
  margin: 5px 0px 0px 0px;
  font-size: 1.1rem;
}

/* 브라우저 너비가 500px에서 699px 사이일 때 */
@media screen and (min-width: 800px) and (max-width: 1299px) {
  .section2 {
    width: 450px !important;
  }
  #chartContainer {
    width: 450px !important;
  }
}
@media screen and (min-width: 1300px) and (max-width: 1799px) {
  .section2 {
    width: 850px !important;
  }
  #chartContainer {
    width: 850px !important;
  }
}
@media screen and (min-width: 1800px) {
  .section2 {
    width: 1250px !important;
  }
  #chartContainer {
    width: 1250px !important;
  }
}
